.main {
    display: grid;
    height: 100vh;
    background-image: linear-gradient(to right, #904fd2 0, #657ced 100%);
}

.modal {
 width: 50%;
 position: absolute;
 top: 50%;
 left: 50%;
 transform: translate(-50%, -50%);
 border-radius: 20px;
 padding: 2em;
 background: white;
}

.h1 {
    color: azure;
}

.h3 {
    color: rgb(255, 255, 255);
}

.p {
    color: rgb(255, 255, 255);
}