@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  background: #000000;
}

body {
  margin: 0;
}

.search-bar {
    width: 100%;
    height: 50px;;
    max-width:50%;
    background: rgba(255, 255, 255, 0.2);
    display: flex;
    align-items: center;
    border-radius: 0px;
    border-color: rgba(12, 96, 221);
    padding: 10px;
  }
  .search-bar input {
    background: transparent;
    flex: 1 1;
    border: 0;
    padding: 24px 20px;
    font-size: 20px;
    color: #cccc;
  }
  ::-webkit-input-placeholder {
    color: whitesmoke;
  }
  ::placeholder {
    color: whitesmoke;
  }
  .search-bar button {
    border: 0;
    border-radius: 50%;
    width: 25px;
    height: 25px;
  }
  

.header{
  display:fixed;
  color: #eee;
  font-family: sans-serif;
  font-size: 10px;
  font-weight: 350;
  margin: top 0;
  height: 50px;
}

.rating_date{
  width:100%;
  display: flex;
  flex-direction:row;
  justify-content: space-evenly;
  align-items: baseline;
}

.p{
  color: #eee;
}
.h4{
  color: #eee;
}


.cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
    grid-gap: 20px;
}

.card {
    display:flex;
    flex-flow:column;
    background-color: #15172b;
    border-radius: 0px;
    box-sizing: border-box;
    height: 450px;
    padding: 5px;
    width: 250px;
    margin: 10px;
    align-items: center;
    
}

.card_img{
  max-height: 300px;
  max-width: 200px;
}

.poster{
  height: 100%;
  width:100%;
}

.content {
 color:#ECF0F1;
 height: 33%;
 overflow:hidden;
}

  

.rating{
  height:8%;
  color: #eee;
}

.genre{
  color: #eee;
  font-family: sans-serif;
  height:16%;
  align-items: center;
}
   


.button-62 {
  background-color: #08d;
  border-radius: 12px;
  border: 0;
  box-sizing: border-box;
  color: #eee;
  cursor: pointer;
  font-size: 18px;
  margin-top: 0px;
  text-align: center;
  width: 60%;
  height: 100%;
}

.button-62:active {
  background-color: #06b;
}

.button-edit{
  display: flex;
  width:auto;
  flex-direction:row;
  flex-wrap: nowrap;
  justify-content: space-between;
  height:10%;
}

.button-63 {
  background-color: #08d;
  border-radius: 12px;
  border: 0;
  box-sizing: border-box;
  color: #eee;
  cursor: pointer;
  font-size: 18px;
  margin-top: 0px;
  text-align: center;
  width: 15%;
  height: 100%;
}
.icon{
  height:50%;
  width:50%;
}

.button-63:active {
  background-color: #06b;
}
.dropbtn{
    position:fixed;
}
.main {
    display: grid;
    height: 100vh;
    background-image: linear-gradient(to right, #904fd2 0, #657ced 100%);
}

.modal {
 width: 50%;
 position: absolute;
 top: 50%;
 left: 50%;
 -webkit-transform: translate(-50%, -50%);
         transform: translate(-50%, -50%);
 border-radius: 20px;
 padding: 2em;
 background: white;
}

.h1 {
    color: azure;
}

.h3 {
    color: rgb(255, 255, 255);
}

.p {
    color: rgb(255, 255, 255);
}
.App {
  text-align: center;
}

.h1{
  color: #ffffff;
}

.header-area {
  height: 100px;;
  width: 100%;
  grid-row-start:1;
  display: flex;
  background-color: rgb(14, 15, 31);
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
}

.container{
  width: 100%;
  height:100%;
  display: grid;
  grid-template-rows:100px auto;
  grid-template-columns:25% auto;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}

.filter-area{
  color: #222222;
  position:static;
  border: 3px solid #141414;
  width: 100%;
  grid-column-start:1;
  grid-row-start:2;
  display:flex;
}

.list-area{
  display: flex;
  grid-row-start:2;
  flex-direction:row;
  flex-wrap: wrap;
  grid-column-start:2;
  border:3px solid #1f1f1f;
}

.page{
  display: flex;
  justify-content: space-around;
  flex-direction:row;
  height: 80px;
  width:100%;
  border:3px solid #1c1c1c;
}

.backdrop {
  background-color: rgb(255, 255, 255);
}

.h1 {
  color: #ffffff;
  display: flex;
  justify-content: center;
}

.pgn {
  width: 100%;
  background-color: rgb(26, 26, 39);
  border-radius: 10px;
  min-height: 40px;
  display: flex;
  align-items: center;
  box-shadow: 0 0 25px 0 rgb(0 0 0 / 4%);
  justify-content: space-between;
}
.page{
  width: 50px;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  height: 30px;
}

